import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import Icon from '@material-ui/core/Icon';
import Chip from '@material-ui/core/Chip';
import LinearProgress from '@material-ui/core/LinearProgress';
import Background from '../Background/Background';

class MoveFolders extends React.Component {
  state = {
    idOutFolder: null,
    idOutFolderName: null,
    idInFolder: null,
    idInFolderName: null,
    isLoading: false,
    toRepeatValue: 0
  };
  handleChangeInput = e => {
    if (e.target.id === 'idOutFolder') {
      let value = e.target.value;
      this.setState({ idOutFolder: value });
      let re = /drive\/folders/g;
      if (value.search(re) !== -1) {
        value = value.split('drive/folders/')[1];
      }
      'google'.script.run
        .withSuccessHandler(folderName => {
          this.setState({ idOutFolderName: folderName });
        })
        .getFolderNameById(value);
    }
    if (e.target.id === 'idInFolder') {
      let value = e.target.value;
      this.setState({ idInFolder: value });
      let re = /drive\/folders/g;
      if (value.search(re) !== -1) {
        value = value.split('drive/folders/')[1];
      }
      'google'.script.run
        .withSuccessHandler(folderName => {
          this.setState({ idInFolderName: folderName });
        })
        .getFolderNameById(value);
    }
  };
  handleChangeSelect = e => {
    this.setState({ toRepeatValue: e.target.value });
  };
  handleClickYes = e => {
    this.setState({ isLoading: true });
    'google'.script.run
      .withSuccessHandler(() => {
        this.setState({ isLoading: false });
      })
      .moveFoldersAndFiles(this.state.idOutFolder, this.state.idInFolder);
    'google'.script.run.setTriggerAtHours(this.state.toRepeatValue);
  };
  handleClickNo = e => {
    this.setState({ isLoading: true });
    'google'.script.run
      .withSuccessHandler(() => {
        this.setState({
          isLoading: false,
          idOutFolder: null,
          idOutFolderName: null,
          idInFolder: null,
          idInFolderName: null,
          isLoading: false,
          toRepeatValue: 0
        });
      })
      .clearAll();
  };
  componentDidMount() {
    'google'.script.run
      .withSuccessHandler(data => {
        console.log(data);
        const {
          idOutFolder,
          idOutFolderName,
          idInFolder,
          idInFolderName,
          toRepeatValue
        } = data;
        this.setState({ idOutFolder });
        this.setState({ idOutFolderName });
        this.setState({ idInFolder });
        this.setState({ idInFolderName });
        this.setState({ toRepeatValue });
        this.setState({ isLoading: false });
      })
      .getData();
  }
  render() {
    return (
      <div>
        {this.state.isLoading ? <Background /> : null}
        {this.state.isLoading ? <LinearProgress /> : null}
        <div>
          <div>
            <TextField
              style={{ width: '100%' }}
              id="idOutFolder"
              label="Из папки"
              placeholder="Укажите URL папки"
              type="search"
              margin="normal"
              defaultValue={this.setState.idOutFolder}
              onChange={this.handleChangeInput}
            />
            {this.state.idOutFolderName ? (
              <a
                href={
                  'https://drive.google.com/drive/folders/' +
                  this.state.idOutFolder
                }
                target="_blank"
                rel="noopener noreferrer"
                style={{ fontSize: '0.7rem' }}
              >
                <Chip
                  icon={<Icon style={{ fontSize: '1rem' }}>folder_open</Icon>}
                  label={this.state.idOutFolderName}
                  onClick="{handleClick}"
                  color="primary"
                  size="small"
                />
              </a>
            ) : null}
          </div>
          <div>
            <TextField
              style={{ width: '100%' }}
              id="idInFolder"
              label="В папку"
              placeholder="Укажите URL папки"
              type="search"
              margin="normal"
              icon={<Icon>add_circle</Icon>}
              onChange={this.handleChangeInput}
              defaultValue={this.setState.idInFolder}
            />
            {this.state.idInFolderName ? (
              <a
                href={
                  'https://drive.google.com/drive/folders/' +
                  this.state.idInFolder
                }
                target="_blank"
                rel="noopener noreferrer"
                style={{ fontSize: '0.7rem' }}
              >
                <Chip
                  icon={<Icon style={{ fontSize: '1rem' }}>folder_open</Icon>}
                  label={this.state.idInFolderName}
                  onClick="{handleClick}"
                  color="primary"
                  size="small"
                />
              </a>
            ) : null}
          </div>
        </div>
        <div>
          <FormControl style={{ width: '100%', marginTop: '18px' }}>
            <InputLabel htmlFor="age-simple">Повторять</InputLabel>
            <Select
              value={this.state.toRepeatValue}
              onChange={this.handleChangeSelect}
            >
              <MenuItem value={0}>
                <em>Нет</em>
              </MenuItem>
              <MenuItem value={1}>Каждый час</MenuItem>
              <MenuItem value={2}>Раз в 3 часа</MenuItem>
              <MenuItem value={6}>Раз в 6 часа</MenuItem>
              <MenuItem value={12}>Раз в 12 часа</MenuItem>
              <MenuItem value={24}>Каждый день</MenuItem>
            </Select>
          </FormControl>
        </div>
        <div>
          <Button
            variant="outlined"
            color="primary"
            size="small"
            style={{ margin: '60px 5px' }}
            onClick={this.handleClickYes}
          >
            Переместить
          </Button>
          <Button
            variant="outlined"
            color="secondary"
            size="small"
            onClick={this.handleClickNo}
          >
            Очистить
          </Button>
        </div>
      </div>
    );
  }
}
export default MoveFolders;
